<template>
	<div class="box">

		<div class="_box" ref="_box" @click="onHandleBox">
			<div class="eara none" @click="onHandleCancel" ref="eara">
				<div class="select">
					<span></span><el-cascader
					    :options="options"
					    v-model="selectedOptions"
					    @change="handleChange">
				 	</el-cascader>
				</div>
				<div class="btn">
					<button class="green_btn" @click="onMeettingSure"></button>
				</div>
			</div>
			<div class="date none" @click="onHandleCancel" ref="_date">
				<el-input-number v-model="meettingNum" :min="1" :max="500"></el-input-number>
				<div class="btn">
					<button class="green_btn" @click="onMeettingSure"></button>
				</div>
			</div>
			<div class="style none"  @click="onHandleCancel" ref="_style">
				<div>
					<p><span>{{pay[0]}} - {{pay[1]}} </span></p>
					<el-slider
				      v-model="pay"
				      range
				      :max="8000">
				    </el-slider>
				</div>
				<div>
					<p><span></span></p>
					<el-rate v-model="star"></el-rate>
				</div>
				<div class="btn">
					<button class="green_btn" @click="onMeettingSure"></button>
				</div>
			</div>
		</div>

		<div class="img">
			<img src="http://image.qmango.com/hotelimg/dl1210/150330/0.jpeg">
		</div>
		<div class="tab">
			<div class="tab_header">
				<div class="header" ref="tab">
					<div v-for='(i,index) in arrItem' :key=index @click='onHandleClick(index,$event)'>{{i}}</div>
				</div>
				<div class="line" ref="line"></div>
			</div>
			<div class="show">
				<div class="tip" v-if="close">
					<p><span class="border"></span><span class="text"></span><span class="close" @click="onHandleClose"><i class="fas fa-times"></i></span></p>
				</div>
				<div v-for="(i,index) in arrItem" v-if="index == index_" class="body">
					<ul>
						<li>
							<span class="border_bottom" @click="onHandleErea">
								<span class="erea">
									{{selectedOptions[1]}}
									<!-- <div class="select">
										<el-cascader
										    :options="options"
										    v-model="selectedOptions"
										    @change="handleChange">
									 	</el-cascader>
									</div> -->
									
								</span>
								<span class="icon"><i class="fas fa-angle-right"></i></span>
							</span>
							
							<span class="right">
								<span class="map"><i class="fas fa-map-marker-alt"></i></span>
								<span></span>
							</span>
						</li>
						<li class="border_bottom">
							<span class="date">3.29</span>
							<span class="day"></span>
							<span class="line">--</span>
							<span class="date">3.30</span>
							<span class="day"></span>
							<span class="icon_"><i class="fas fa-chevron-right"></i></span>
							<!-- <div class="date_value">
								<el-date-picker
							      v-model="date_value"
							      type="date"
							      placeholder="" 
							      :picker-options="pickerOptions1">
							    </el-date-picker>
							</div>
							<div class="date_value input">
								<p class="num">
									<el-input type="number" v-model="input" placeholder=""></el-input>
								</p>
							</div> -->
							
						</li>   
						<li class="border_bottom" @click="onHandleMeetting">
							<span class="text">{{i}} : {{meettingNum}}</span>
							<span class="icon_"><i class="fas fa-chevron-right"></i></span>
							<!-- <p>
								<el-input placeholder="" v-model="meetting_total" type="number">
							    <template slot="prepend">{{i}}:</template>
							  </el-input>
							</p> -->
						</li>
						<li class="border_bottom" @click="onHandleStyle">
							<span class="text">{{pay[0]}} - {{pay[1]}}  /  {{star}} </span>
							<span class="icon_"><i class="fas fa-chevron-right"></i></span>
							<!-- <div class="pay">
								<span>{{pay[0]}} - {{pay[1]}}</span>
							</div>
							<div>
								<el-slider
							      v-model="pay"
							      range
							      show-stops
							      :max="3000">
							    </el-slider>
							</div> -->
						</li>
						<!-- <li>
							<div class="star">
								<p class="title"></p>
								<p class="startCheck">
									<span><el-radio v-model="starType" label="no"></el-radio></span>
									<span><el-radio v-model="starType" label="1"></el-radio></span>
									<span><el-radio v-model="starType" label="2"></el-radio></span>
									<span><el-radio v-model="starType" label="3"></el-radio></span>
									<span><el-radio v-model="starType" label="4"></el-radio></span>
									<span><el-radio v-model="starType" label="5"></el-radio></span>
								</p>
							</div>
						</li> -->
					</ul>
					<p class="btn"><button></button></p>
					<div class="bottom">
						<div>
							<span><i class="fas fa-history"></i></span>
							<span></span>
						</div>
						<div>
							<span><i class="far fa-star"></i></span>
							<span></span>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import common from '../../common/js/common'
	export default {
		mounted: function(){
			this.$refs.line.style.marginLeft = this.$refs.tab.firstChild.offsetLeft + 'px'
			this.$refs._box.style.width = window.innerWidth + 'px'
		},
		data(){
			return {
				arrItem: [
					'',
					'',
					''
				],
				index_: 0,
				close: true,
				control: true,
				options: [
					{
			          value: '',
			          label: '',
			          children: [
				          {
				            value: '',
				            label: ''
				          },
				          {
				            value: '',
				            label: ''
				          },
				          {
				            value: '',
				            label: ''
				          },
				          {
				            value: '',
				            label: ''
				          },
			          ]
			         }
				],
				pickerOptions1: {
		            disabledDate(time) {
		              return time.getTime() <= (Date.now()-1000*60*60*24);
		            }
		          },
				selectedOptions: ['',''],
				meettingNum: 1,
				star: 3,
				date_value: '',
				input: '',
				meetting_total: '',
				pay: [80,800],
				starType: 'no'
			}
		},
		methods: {
			onHandleClick(i,event){
				event.path[2].lastElementChild.style.marginLeft = event.path[0].offsetLeft + 'px'
				event.path[2].lastElementChild.style.width = common.getStyle(event.path[0],'width')
				this.index_ = i
			},
			onHandleClose(){
				this.close = false
			},
			handleChange(value) {
		        // console.log(value);
		    },
		    onHandleErea(){
		    	this._boxShow()
		    	this.show(this.$refs.eara)
		    },
		    onHandleBox(){
		    	this._boxHide()
		    	this.hide(this.$refs.eara)
		    	this.hide(this.$refs._date)
		    	this.hide(this.$refs._style)
		    },
		    onHandleMeetting(){
		    	this._boxShow()
		    	this.show(this.$refs._date)
		    },
		    onHandleStyle(){
		    	this._boxShow()
		    	this.show(this.$refs._style)
		    },
		    onMeettingSure(){
		    	this.onHandleBox()
		    },
		    _boxShow(){
		    	this.$refs._box.style.height = window.innerHeight + 'px'
		    },
		    _boxHide(){
		    	this.$refs._box.style.height = '0px'
		    },
		    show(obj){
		    	obj.style.display = 'block'
		    },
		    hide(obj){
		    	obj.style.display = 'none'
		    },
		    onHandleCancel(event){
		    	event.cancelBubble = true
		    },
		    onSure(){
		    	this.onHandleBox()
		    }
		}
	}
</script>
<style scoped lang="scss">
	@import '../../common/css/common.scss';
	.box{
		background-color: #ededed;
		width: 100%;

		._box{
			background-color: rgba(0,0,0,0.5);
			position: absolute;
			left: 0;
			top: 0;
			z-index: 999;
			>div{
				background-color: #fff;
				padding: rem(5px) rem(8px);
				margin: rem(100px) auto;
				width: 85%;
				text-align: center;
			}
			.none{
				display: none;
			}
			.btn{
				padding: rem(12px) rem(25px);
			}
			.eara{
				.select{
					display: inline-block;
					padding: rem(5px) rem(8px);
					font-size: rem(14px);
					color: #aaa;
				}
				
			}
			.date{
				padding-top: rem(13px);
			}
			.style{
				padding-top: rem(10px);
			}
		}

		.img{
			img{
				width: 100%;
				height: 200px;
			}
		}
		.close{
			float: right;
			color: #aaa;
			padding: rem(2px) rem(5px);
		}
		.tab{
			background-color: #fff;
			.tab_header{
				
				position: relative;
				.header{
					display: flex;
					justify-content: space-around;
					align-items:center;
					border-bottom: 1px solid #aaa;
					padding: rem(13px) 0 rem(15px);
				}
				.line{
					position: absolute;
					left: 0;
					bottom: 0; 
					width: rem(30px);
					height: rem(5px);
					background-color: #8BC34A;
					border-radius: rem(5px);
					transition: all 0.8s;
				}
			}
			.show{
				.tip{
					background-color: #EDEDED;
					padding: rem(10px) rem(20px);
					p{
						.border{
							display: inline-block;
							padding: rem(1px) rem(2px);
							border: 1px solid #FF9800;
							color: #FF9800;
						}
						.text{
							margin-left: rem(15px);
							color: #FF9800;
						}
					}
				}
				.body{
					padding: rem(10px) rem(10px);
					ul{
						li{
							padding: rem(10px) 0;
							.erea{
								font-size: rem(18px);
							}
							.icon{
								margin-left: 50%;
								color: #aaa;
								padding: 0 rem(10px);
							}
							.right{
								margin-left: 3%;
								.map{
									color: #FF9800;
								}
							}
							.date{
								font-size: rem(20px);
								font-weight: bold;
							}
							.line{
								display: inline-block;
								padding: 0 rem(10px);
							}
							.icon_{
								float: right;
								display: inline-block;
								padding: 0 rem(10px);
								color:#aaa;
							}
							.text{
								color: #aaa;
							}
							.date_value{
								display: inline-block;
								width: 49%;
								p{
									display: inline-block;
									&.num{
										width: 80%;
										margin-left: 25%;
									}
								}
							}
							.pay{
								text-align: center;
								color: #aaa;
								font-size: rem(14px);
							}
							.star{
								padding: rem(3px) rem(8px);
								.title{
									font-size: rem(14px);
									padding: rem(8px) 0;
								}
								.startCheck{
									span{
										display: inline-block;
										padding: rem(5px) rem(5px);
									}
								}
							}
							
						}
					}
					.border_bottom{
						border-bottom: 1px solid #aaa;
						padding-bottom: rem(10px);
					}
					.btn{
						margin-top: rem(15px);
						button{
							width: 100%;
							border: none;
							background-color: #8BC34A;
							padding: rem(13px) 0;
							color: #fff;
							border-radius: rem(5px);
						}
					}
					.bottom{
						display: flex;
						justify-content: space-around;
						align-items: center;
						margin-top: rem(15px);
						div{
							padding: rem(15px) 0;
						}
					}
				}
			}
		}
	}
</style>


