					<div class="select_1">
						<el-select v-model="select6" slot="prepend" placeholder="">
					      <el-option label="" value="1"></el-option>
					      <el-option label="" value="2"></el-option>
					      <el-option label="" value="3"></el-option>
					    </el-select>
					</div>
				</li>
				<li class="item_1">
				    <span class="name">:</span>
