					</div>
				</li>
				<li class="item">
					<span class="name">:</span>
					<div class="check">
						  <el-checkbox-group v-model="checkList">
						    <el-checkbox label="24"></el-checkbox>
						    <el-checkbox label=""></el-checkbox>
						    <el-checkbox label=""></el-checkbox>
						    <el-checkbox label=""></el-checkbox>
						    <el-checkbox label=""></el-checkbox>
						  </el-checkbox-group>
