<script>
	export default {
		data(){
			return {
				  input3: '',
			      input4: '',
			      input5: '',
			      select3: '60',
			      select4: '1',
			      select5: '1.2',
			      select6: '',
			      checkList: ['','24']
			}
		},
		methods: {

		}
	}
</script>
<style scoped lang="scss">
	@import "../../../common/css/common.scss";
	.box{
		width: 100%;
		.body{
			padding: 0 rem(16px);
			.item{
				border-bottom: #e5e5e5 solid rem(1px);
				padding: rem(15px) 0;
